.mask{
    width: 100%;
    height: 100vh;
    position: relative;
}

.intro-img{
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}

.mask::after{
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #000;
    opacity: 0.8;
}

.body{
    width: 100%;
    height: 100%;
}

.body .content{
    position: absolute;
    transform: translate(-50%, -50%);
    text-align: center;
    top: 50%;
    left: 50%;
}

.body .content h1{
    font-size: 2.5rem;
    padding: 0.6rem 0 1.5rem;
}

.body .content p{
    font-size: 1rem;
    font-weight: 200;
    text-transform: uppercase;
}

.content .btn {
    margin: 1rem 0.3rem;
}


@media screen and (max-width:1040px){
    .body .content h1{
        font-size: 2rem;
    }
    
    .body .content p{
        font-size: 1rem;

    }
}